html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "GothamPro";
  font-style: normal;
  font-weight: 400;
  src: local("GothamPro"), local("GothamPro"), url("../fonts/GothamPro.woff") format("woff"), url("../fonts/GothamPro.woff2") format("woff2");
}

@font-face {
  font-family: "GothamPro";
  font-style: normal;
  font-weight: 600;
  src: local("GothamPro-Medium"), local("GothamPro-Medium"), url("../fonts/GothamPro-Medium.woff") format("woff"), url("../fonts/GothamPro-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: local("GothamPro-Bold"), local("GothamPro-Bold"), url("../fonts/GothamPro-Bold.woff") format("woff"), url("../fonts/GothamPro-Bold.woff2") format("woff2");
}

@font-face {
  font-family: "GothamPro";
  font-style: normal;
  font-weight: 800;
  src: local("GothamPro-Black"), local("GothamPro-Black"), url("../fonts/GothamPro-Black.woff") format("woff"), url("../fonts/GothamPro-Black.woff2") format("woff2");
}

@font-face {
  font-family: "GothamPro";
  font-style: italic;
  font-weight: 800;
  src: local("GothamPro-BlackItalic"), local("GothamPro-BlackItalic"), url("../fonts/GothamPro-BlackItalic.woff") format("woff"), url("../fonts/GothamPro-BlackItalic.woff2") format("woff2");
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  min-width: 320px;
  line-height: 1.4;
  font-family: "GothamPro";
}

.wrapper {
  width: 100%;
  height: 100vh;
}

.inner {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 20px;
}

.title-lg {
  font-size: 30px;
  font-weight: 800;
  font-family: "GothamPro";
  text-transform: uppercase;
  color: #ffffff;
}

@media only screen and (max-width: 767px) {
  .title-lg {
    font-size: 20px;
  }
}

.title-s {
  font-size: 14px;
  font-weight: 600;
  font-family: "GothamPro";
  text-transform: uppercase;
  color: #ffffff;
}

@media only screen and (max-width: 767px) {
  .title-s {
    font-size: 12px;
  }
}

.gradient {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: url(../img/gradient.jpg);
  background-repeat: no-repeat;
  background-size: 120% 120%;
  background-position: 50% 50%;
  overflow: hidden;
}

@media only screen and (max-width: 767px) {
  .gradient {
    min-height: auto;
    overflow: auto;
  }
}

.gradient__inner {
  position: relative;
  height: 100%;
  z-index: 2;
}

.gradient__wrap {
  position: absolute;
  top: 50%;
  left: 15px;
  right: 15px;
  transform: translateY(-50%);
}

.gradient__img {
  position: absolute;
  z-index: 1;
  font-size: 0;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: 50% 50%;
  overflow: hidden;
}

.gradient__img img {
  width: 100%;
}

.gradient__img--first {
  bottom: 30px;
  right: 0;
  width: 800px;
  height: 105%;
  background-image: url(../img/element.png);
}

@media only screen and (max-width: 767px) {
  .gradient__img--first {
    display: none;
  }
}

.gradient__img--second {
  bottom: 0;
  right: 40px;
  width: 370px;
  height: 605px;
  background-image: url(../img/game.png);
}

@media only screen and (max-width: 1023px) {
  .gradient__img--second {
    right: 30px;
  }
}

@media only screen and (max-width: 767px) {
  .gradient__img--second {
    display: none;
  }
}

.header {
  padding: 50px 0;
}

@media only screen and (max-width: 767px) {
  .header {
    padding: 15px 0;
  }
}

.logo {
  display: block;
  max-width: 172px;
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .logo {
    max-width: 132px;
  }
}

.logo img {
  width: 100%;
}

.hero {
  max-width: 660px;
  margin-right: 440px;
  padding-bottom: 70px;
}

@media only screen and (max-width: 1023px) {
  .hero {
    margin-right: 400px;
    padding-bottom: 15px;
  }
}

@media only screen and (max-width: 767px) {
  .hero {
    max-width: 100%;
    margin-right: 0;
  }
}

.hero__title {
  margin-bottom: 35px;
}

@media only screen and (max-width: 767px) {
  .hero__title {
    margin-bottom: 10px;
  }
}

.hero__title span {
  text-transform: none;
  font-style: italic;
}

.hero__description {
  font-size: 14px;
  color: #ffffff;
  margin-bottom: 15px;
}

@media only screen and (max-width: 767px) {
  .hero__description {
    font-size: 12px;
  }
}

.hero__title-form {
  max-width: 340px;
  margin-bottom: 35px;
}

@media only screen and (max-width: 767px) {
  .hero__title-form {
    margin-bottom: 20px;
  }
}

.form__row {
  max-width: 270px;
}

.form__row:not(:last-child) {
  margin-bottom: 10px;
}

@media only screen and (max-width: 767px) {
  .form__row {
    max-width: 100%;
  }
}

.form__input {
  width: 100%;
  height: 32px;
  padding: 9px 0;
  font-size: 14px;
  color: #ffffff;
  border-bottom: 2px solid #ffffff;
  background: transparent;
  box-sizing: border-box;
}

.form__input::placeholder {
  font-family: "GothamPro";
  font-size: 14px;
  color: #ffffff;
}

@media only screen and (max-width: 767px) {
  .form__input {
    font-size: 12px;
  }
  .form__input::placeholder {
    font-size: 12px;
  }
}

.btn {
  position: relative;
  margin-top: 20px;
  border: none;
  outline: none;
  background: none;
  width: 100%;
  padding: 15px;
  background-color: #ffffff;
  border-radius: 20px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 11px;
  color: #000000;
  overflow: hidden;
  z-index: 1;
}

@media only screen and (max-width: 767px) {
  .btn {
    margin-top: 10px;
  }
}

.btn:after {
  position: absolute;
  content: '';
  top: 0;
  bottom: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background-color: #efcb19;
  border-radius: 20px;
  transform: translate(0);
  transition: background-color .3s, transform .3s;
  z-index: -1;
}

.btn:hover:after {
  transform: translate(100%);
}
