.form {
    &__row {
        max-width: 270px;
        &:not(:last-child) {
            margin-bottom: 10px; }
        @include sm {
            max-width: 100%; } }
    &__input {
        width: 100%;
        height: 32px;
        padding: 9px 0;
        font-size: 14px;
        color: $white;
        border-bottom: 2px solid $white;
        background: transparent;
        box-sizing: border-box;
        @include placeholder {
            font-family: $gothamPro;
            font-size: 14px;
            color: $white; }
        @include sm {
            font-size: 12px;
            @include placeholder {
                font-size: 12px; } } } }
