//FONTS

@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1),
	url("../fonts/" + $file + ".woff") format("woff"),
	url("../fonts/" + $file + ".woff2") format("woff2") {} }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//START

@font-face {
	@include fontdefault(normal, 400, 'GothamPro');
	@include fontpath('GothamPro', 'GothamPro', 'GothamPro'); }

@font-face {
	@include fontdefault(normal, 600, 'GothamPro');
	@include fontpath('GothamPro-Medium', 'GothamPro-Medium', 'GothamPro-Medium'); }

@font-face {
	@include fontdefault(normal, 700, 'Montserrat');
	@include fontpath('GothamPro-Bold', 'GothamPro-Bold', 'GothamPro-Bold'); }

@font-face {
	@include fontdefault(normal, 800, 'GothamPro');
	@include fontpath('GothamPro-Black', 'GothamPro-Black', 'GothamPro-Black'); }

@font-face {
	@include fontdefault(italic, 800, 'GothamPro');
	@include fontpath('GothamPro-BlackItalic', 'GothamPro-BlackItalic', 'GothamPro-BlackItalic'); }
